import React from 'react'
import ReactDOM from 'react-dom'
import { ChainId, Config, DAppProvider, MULTICALL_ADDRESSES } from '@usedapp/core'
import { App } from './App'
import 'bootstrap/dist/css/bootstrap.min.css';
import {store} from './store'
import { Provider } from 'react-redux'


const config:Config = {
  readOnlyChainId: ChainId.Mainnet,
  readOnlyUrls: {
    [ChainId.Mainnet]: 'https://mainnet.infura.io/v3/3165a249c65f4198bf57200109b8fadf',
    [ChainId.Hardhat]: 'http://127.0.0.1:8545/'
  },
  supportedChains:[ChainId.Mainnet, ChainId.Kovan, ChainId.Hardhat, ChainId.Polygon, ChainId.Mumbai],
}

ReactDOM.render(
  <React.StrictMode>
    <Provider store={store}>
    <DAppProvider config={config}>
      <App />
    </DAppProvider>
    </Provider>,
  </React.StrictMode>,
  document.getElementById('root')
)
